@import 'base';


/****/
@media only screen and (max-width: 767px) {
	.font-xs *{
		font-size: 14px;
	}
}
@media only screen and (min-width: 768px) and (max-width: 992px) {
	.font-sm *{
		font-size: 16px;
	}
}
@media only screen and (min-width: 992px) and (max-width: 1200px){
	.font-md *{
		font-size: 18px;
	}
}
@media only screen and (min-width: 1200px) {
	.font-lg *{
		font-size: 20px;
	}
}

/****/
@media (max-width: 767px) {
	.hidden-xs {
		display: none !important;
	}
}
@media (min-width: 768px) and (max-width: 970px) {
  .hidden-sm {
		display: none !important;
	}
}
@media (min-width: 970px) and (max-width: 1200px) {
  .hidden-md {
		display: none !important;
	}
}
@media (min-width: 1200px) {
	.hidden-lg {
		display: none !important;
	}
}

/*
================================
  
================================
*/
/****/
.list-inline > li{
	display: inline-block;
	padding-left: 10px;
	padding-right: 10px;
	vertical-align: middle;
}

/*
================================
	
================================
*/
.left{
	float: left;
}
.right{
	float: right;
}
.relative{
	position: relative;
}
.absolute{
	position: absolute;
}
.fixed{
	position: fixed;
}
.hidden{
	display: none;
}
.block{
	display: block;
}
.inlineblock{
	display: inline-block;
}
.text-center{
	text-align: center;
}
/****/
.active {
	color: $global-color;
}
/****/
.clearfix{
	zoom: 1;

	&:before,&:after{
		display: table;
		line-height: 0;
		content: "";
	}
	&:after{
		clear: both;
	}
}
/*
=============================
	
=============================
*/
/****/
.icon-set{
	color: #b4b4b4;
	font-size: 14px;
}
/**label**/
.label{
	border: 1px solid #683047;
	padding: 3px 5px;
	border-radius: 5px;
	margin-right: 10px !important;
	font-size: 14px;
	&:hover{
		border: 1px solid #ea6f5a;
	}
}
.avatar{
	vertical-align: middle;
	margin-right: 5px;
	width: 32px;
	height: 32px;

	img {
		width: 100%;
		height: 100%;
		border: 1px solid #ddd;
		border-radius: 50%;
	}
} 


